.contact-container {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
}

.contact-copy {
    width: 275px;
}

.icons {
  -ms-flex-item-align: center;
      align-self: center;
  margin-bottom: 30px;
}

.fa {
    color: #007bff; 
  }
  
  .fa-twitter-square,
  .fa-github-square {
    margin-right: .25em; 
  }
  
  .social:hover {
    -webkit-transform: scale(1.1);
        -ms-transform: scale(1.1);
            transform: scale(1.1); 
  }

  @media (min-width: 374px) {
    .contact-copy {
      font-size: 18px;
      width: 290px;
    }
  }
  
  @media (min-width: 400px) {
    .contact-copy {
      width: 300px;
    }
  }

  @media (min-width: 765px) {
    .contact-container {
      margin-top: 27px;
    }
  }


