.project-container {
    -ms-flex-align: center;
        align-items: center;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
}

.subtitle {
    margin: 20px 0 5px 0;
    text-align: center;
    width: 100%;
}

.thumbnail {
    border-radius: 10px;
    margin-top: 20px;
    height: initial;
    width: 280px;
}

@media (min-width: 374px) {
    .thumbnail {
        width: 300px;
    }
}

@media (min-width: 765px) {
    .project-container {
        -ms-flex-direction: row;
            flex-direction: row;
        -ms-flex-wrap: wrap;
            flex-wrap: wrap;
        -ms-flex-pack: space-evenly;
            justify-content: space-evenly;
        margin-top: 23px;
        width: 768px;
    }

    .subtitle {
        font-size: 37px;
        font-weight: 400;
    }

    .thumbnail {
        width: 350px;
    }
}


